module.exports = {
	info : function () {
		try {
		    const res = uni.getSystemInfoSync();
			var iPhoneXBottom = 0;
			if(res.model.indexOf('iPhoneX') != -1){
				res.iPhoneXBottomHeightRpx = 72;
				res.iPhoneXBottomHeightPx = uni.upx2px(72);
			}else{
				res.iPhoneXBottomHeightRpx = 0;
				res.iPhoneXBottomHeightPx  = 0;
			}
			return res;
		} catch (e){
		    return null;
		}
	}
}